<template>
  <Row style="max-width: 600px; margin: 0 auto;">
    <Row type="flex" justify="center">
      <Card style="width: 100%;">
        <h3></h3>
        <Form status-icon :model="formData" :rules="rules" ref="formData">
          <FormItem label="" prop="keyword">
            <Input type="text" v-model="formData.keyword" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="programName">
            <Select v-model="formData.programName" placeholder="">
              <Option label="6" value="xkjdl"></Option>
              <Option label="" value="xkyb"></Option>
              <Option label="" value="xklb"></Option>
            </Select>
          </FormItem>
          <FormItem>
            <Button @click="search"></Button>
            <Button @click="clear"></Button>
            <Button @click="help = !help"></Button>
          </FormItem>
        </Form>
      </Card>
    </Row>
    <Row>
      <Card v-if="help">
        <h4>6</h4>
        <Table :show-header="false" border :columns="jdlhelpColumns" :data="jdlHelp"></Table>
      </Card>
    </Row>
    <Card>
      <Row>
        <Table :columns="contentColumns" stripe :data="content" :loading="isSearch">
        </Table>
      </Row>
    </Card>
  </Row>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      formData: {
        keyword: "",
        programName: ""
      },
      rules: {
        keyword: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        programName: [
          { validator: this.getProgram, required: true, trigger: "change" }
        ]
      },
      jdlhelpColumns: [
        {
          title: "1",
          key: "1"
        },
        {
          title: "2",
          key: "2"
        },
        {
          title: "3",
          key: "3"
        },
        {
          title: "4",
          key: "4"
        }
      ],
      contentColumns: [
        {
          title: "",
          type: "index",
          width: 60
        },
        {
          title: "",
          key: "word"
        },
        {
          title: "",
          key: "pronunciation"
        },
        {
          title: "",
          key: "code"
        },
        {
          title: "",
          key: "split"
        }
      ],
      alertType: "info",
      alertInfo: "",
      help: false,
      contnum: 0,
      program: [],
      oldProgram: "",
      content: [],
      isSearch: false,
      jdlHelp: [
        {
          "1": "/<br>a",
          "2": "/<br>i",
          "3": "<br>o",
          "4": "<br>v"
        },
        {
          "1": "/<br>ii",
          "2": "<br>ao",
          "3": "/<br>vo",
          "4": "/<br>io"
        },
        {
          "1": "<br>oi",
          "2": "<br>uo",
          "3": "<br>iu"
        }
      ]
    };
  },
  methods: {
    search() {
      this.$refs["formData"].validate(valid => {
        if (valid) {
          var __this = this;
          this.isSearch = true;
          setTimeout(() => {
            var keyw = this.formData.keyword.split("");
            /*  */
            var result = 1;
            /*  */
            for (var txt in keyw) {
              for (var key in __this.program) {
                if (__this.program[key].word == keyw[txt]) {
                  __this.content.push({
                    num: result++,
                    word: keyw[txt],
                    pronunciation: __this.program[key].pronunciation,
                    code: __this.program[key].code,
                    split: __this.program[key].split
                  });
                  __this.contnum++;
                }
              }
              result = 1;
            }
            this.isSearch = false;
          }, 300);
        }
      });
    },
    clear() {
      for (let i = 0; i < this.contnum; i++) {
        this.content.pop();
      }
    },
    getProgram(rule, value, callback) {
      if (value === "") {
        callback(new Error(""));
      } else {
        this.$Message.loading({
          content: ""
        });
        this.program = require("./" + value + ".json");

        var programSelect =
          { xkjdl: "6", xkyb: "", xklb: "" }[value] || "";

        this.$Message.destroy();
        this.clear();
        if (this.oldProgram != value && this.program) {
          this.$Message.success(`${programSelect}`);
        }
        this.oldProgram = value;
        callback();
      }
    }
  }
};
</script>

<style>
</style>
