<template>
	<Row type="flex" justify="center">
		<Row style="max-width: 1200px; padding: 0 20px;">
			<Card>
				<Row class="main-textarea-box" type="flex" justify="center">
					<Col :xs="24" :sm="8">
					<div class="info" @click="showPlate.old = !showPlate.old">
						<span class="textarea-title"></span>
						<span class="textarea-info" v-html="count.old"></span>
					</div>
					<transition name="slide">
						<textarea spellcheck="false" v-show="showPlate.old" v-model="oldTerms" @keydown.tab.prevent="insertTab" @keyup.ctrl.86="testing(false)"
							:placeholder="oldAttr"></textarea>
					</transition>
					</Col>
					<Col :xs="24" :sm="8">
					<div class="info" @click="showPlate.new = !showPlate.new">
						<span class="textarea-info" v-html="count.new"></span>
						<span class="textarea-title"></span>
					</div>
					<transition name="slide">
						<textarea spellcheck="false" v-show="showPlate.new" v-model="newsTerms" @keydown.tab.prevent="insertTab" @keyup.ctrl.86="testing(false)"
							:placeholder="newAttr"></textarea>
					</transition>
					</Col>
					<Col :xs="24" :sm="8">
					<div class="info" @click="showPlate.out = !showPlate.out">
						<span class="textarea-info" v-html="count.out"></span>
						<span class="textarea-title"></span>
					</div>
					<transition name="slide">
						<textarea spellcheck="false" v-show="showPlate.out" readonly v-model="outTerms" placeholder=""></textarea>
					</transition>
					</Col>
					<Col :xs="24" :sm="12">
					<div class="info" @click="showPlate.suc = !showPlate.suc">
						<span class="textarea-info" v-html="count.success"></span>
						<span class="textarea-title"></span>
					</div>
					<transition name="slide">
						<textarea spellcheck="false" v-show="showPlate.suc" readonly v-model="successInfo" placeholder=""></textarea>
					</transition>
					</Col>
					<Col :xs="24" :sm="12">
					<div class="info" @click="showPlate.err = !showPlate.err">
						<span class="textarea-info" v-html="count.error"></span>
						<span class="textarea-title"></span>
					</div>
					<transition name="slide">
						<textarea spellcheck="false" v-show="showPlate.err" readonly v-model="errorInfo" placeholder=""></textarea>
					</transition>
					</Col>
				</Row>
			</Card>

			<i-switch class="controls-switch" v-model="switchControl" @click="switchControl = !switchControl"></i-switch>

			<transition name="slide">
				<Card class="controls-btn" style="padding: 0 20%;" v-if="switchControl">
					<Row type="flex" justify="space-around" align="middle">
						<Button size="large" @click="testing(true)" v-if="!handleTermsWorker"></Button>
						<Button size="large" @click="proStartStatus" type="info" v-if="handleTermsWorker"></Button>
						<Tooltip content="" placement="top">
							<i-switch size="large" v-model="isDev">
								<span slot="open"></span>
								<span slot="close"></span>
							</i-switch>
						</Tooltip>
						<Tooltip content="" placement="top">
							<Button size="large" @click="createDemo"></Button>
						</Tooltip>
						<Button size="large" type="error" @click="clearContent"></Button>
					</Row>
				</Card>
			</transition>

			<div class="bottom-info w-100">
				<Card>
					<Row type="flex" justify="center">
						<h3>v{{version}}</h3>
					</Row>
				</Card>
				<Card>
					<Row type="flex" justify="center">
						<p>BashShellsortsh
							<Button to="https://gitee.com/nmlixa/Rime_JD/tree/master/Tools/TermsTools"
								target="_blank">1sortTerms.sh</Button>
						</p>
						<p>
							<b></b></p>
					</Row>
				</Card>
				<Card class="history-card">
					<h4 slot="title"></h4>
					<Button slot="extra" @click="updateAll = !updateAll"></Button>
					<Row>
						<Timeline>
							<TimelineItem>
								<Tag color="success">new</Tag>{{updateHistory[updateHistoryLength].ver}}: {{updateHistory[updateHistoryLength].cont}}
							</TimelineItem>
							<TimelineItem>
								<Tag>new</Tag>{{updateHistory[updateHistoryLength-1].ver}}: {{updateHistory[updateHistoryLength-1].cont}}
							</TimelineItem>
						</Timeline>
					</Row>
					<Row v-if="updateAll">
						<Timeline>
							<TimelineItem v-for="item in updateHistory" :key="item.cont">
								{{item.ver}}: {{item.cont}}
							</TimelineItem>
						</Timeline>
					</Row>
				</Card>
			</div>
		</Row>
	</Row>
</template>

<script>
export default {
  name: 'Contribution',
  data() {
    return {
      version: -1,
      oldTerms: '',
      newsTerms: '',
      oldAttr: '\n\t',
      newAttr:
        '\n\t[ +() -() *() /() ]\t',
      btnInfo: '',
      outTerms: '',
      successInfo: '',
      errorInfo: '',
      oldTermsData: {},
      newsTermsData: {},
      newTermsData: '',
      newTermsCountData: {},
      successInfoData: '',
      count: {
        old: ': 0 : 0',
        new: ': 0 : 0 : 0',
        out: ': 0 : 0',
        success: '0 : 0 : 0 0',
        error: ': 0 : 0 0 0',
      },
      errorInfoData: '',
      demoData: {
        old: `	blke
	jglk
	jglk
6	blke
6	wffj
	fzxi
	wffj`,
        new: `blke	+	
blkeav#2	*	
hjlh	/	
jglk#3	-	
jglk#4	+	
wffj	-	`,
      },
      updates: false,
      isPhone: false,
      isDev: false,
      isLog: false,
      switchControl: true,
      showPlate: {
        old: true,
        new: true,
        out: false,
        suc: false,
        err: false,
      },
      updateAll: false,
      handleTermsWorker: null,
      updateHistory: [
        {
          ver: '1.0',
          cont: '',
        },
        {
          ver: '1.1',
          cont: '',
        },
        {
          ver: '1.2',
          cont: '*!m',
        },
        {
          ver: '1.3',
          cont: '*!m',
        },
        {
          ver: '1.4',
          cont: '!m*',
        },
        {
          ver: '1.5',
          cont: '',
        },
        {
          ver: '1.6',
          cont: '',
        },
        {
          ver: '1.6.1',
          cont: '',
        },
        {
          ver: '1.7',
          cont:
            '  ',
        },
        {
          ver: '1.8',
          cont: '',
        },
        {
          ver: '2.0',
          cont:
            '',
        },
        {
          ver: '2.1',
          cont: '',
        },
        {
          ver: '2.2',
          cont:
            '',
        },
        {
          ver: '2.3',
          cont: 'Bug',
        },
        {
          ver: '2.4',
          cont:
            '',
        },
        {
          ver: '2.5',
          cont: '',
        },
        {
          ver: '3.0',
          cont:
            '#',
        },
        {
          ver: '4.0 Alpha',
          cont: 'BootstrapiViewUI',
        },
        {
          ver: '4.0',
          cont: 'Web Worker',
        },
        {
          ver: '4.1',
          cont:
            'Chrometextarea1000',
        },
        {
          ver: '4.1.1',
          cont: '',
        },
        {
          ver: '4.1.2',
          cont: ' /',
        },
        {
          ver: '4.1.2.1',
          cont: '\'tab\'',
        },
      ],
      updateHistoryLength: 0,
    };
  },
  created() {
    this.isPhoneFn();
    this.getVersion();
  },
  mounted() {
    this.Notice();
  },
  methods: {
    getVersion() {
      this.version = this.updateHistory[this.updateHistoryLength].ver;
    },
    handleOldTerms(newsHaveRepeat) {
      var thisData = {};

      thisData.old = this.oldTerms;
      thisData.oldArr = this.oldTerms.split('\n');
      thisData.new = this.newTermsData;
      thisData.isLog = this.isLog;

      this.$Loading.start();
      this.$Message.destroy();
      this.$Message.loading({
        content: `${
          this.oldTermsData.obj.word.length
        }`,
        duration: 0,
      });
      this.handleTermsPro &&
        this.$Message.loading({
          content: '',
          duration: 0,
        });

      this.handleTermsWorker = null;
      //1 ,
      this.handleTermsWorker = this.$worker
        .run(
          (newsHaveRepeat, {oldArr, old}) => {
            let out = '';
            if (newsHaveRepeat) {
              let dict = {};
              for (let it of oldArr) {
                it = it.trim();
                it = it.split(/\s+/);
                let c = it.pop().trim();
                dict[c] ? dict[c].push(it[0]) : (dict[c] = it);
              }
              for (let i in dict) {
                let idx = 1;
                while (dict[i].length) {
                  out += `${dict[i].shift()}\t${i}#${idx}\n`;
                  idx++;
                }
              }
            } else {
              out = old;
            }
            return out;
          },
          [newsHaveRepeat, thisData]
        )
        .then(res => {
          this.newTermsData = res;
          this.TermsCountSet('oldTerms', 'oldTermsData');
          this.handleTerms();
          this.handleTermsWorker = null;
        })
        .catch(e => {
          this.$Loading.error();
          this.$Message.destroy();
          this.$Message.error({
            content: `${e}`,
            duration: 0,
          });
          console.log(e);
          this.handleTermsWorker = null;
        });
    },
    handleTerms() {
      //
      var reg, out, log, HaveCodeReg, TermsCorrectCodeReg;
      var thisNewData = this.newsTermsData.obj,
        x = 0,
        num = {
          suc: 0,
          errall: 0,
          add: 0,
          mod: 0,
          del: 0,
          err: 0,
          no: 0,
          erratt: 0,
        };

      //2 
      for (x in thisNewData.word) {
        //add
        if (thisNewData.opreation[x] == '+') {
          //
          reg = new RegExp(`.+\\t\\b${thisNewData.code[x]}\\b`, 'g');
          log = `[ ${thisNewData.code[x]}\t${thisNewData.opreation[x]}\t${
            thisNewData.word[x]
          } ]`;
          out = thisNewData.word[x] + '\t' + thisNewData.code[x];

          if (this.newTermsData.search(reg) == -1) {
            this.newTermsData += out + '\r\n';
            this.successInfoData += `${out}\t[ + ${+x + 1}]\r\n`;
            num.suc++;
            num.add++;
          } else {
            this.errorInfoData += `[${+x +
              1}] \t${log}\r\n>> Error \r\n${this.newTermsData
              .match(reg)
              .join('')}\r\n\r\n`;
            num.errall++;
            num.err++;
          }
          //modify
        } else if (thisNewData.opreation[x] == '*') {
          reg = new RegExp(`.+\\t\\b${thisNewData.code[x]}\\b`, 'g');
          log = `[ ${thisNewData.code[x]}\t${thisNewData.opreation[x]}\t${
            thisNewData.word[x]
          } ]`;
          out = thisNewData.word[x] + '\t' + thisNewData.code[x];

          if (this.newTermsData.search(reg) != -1) {
            this.newTermsData = this.newTermsData.replace(reg, out);
            this.successInfoData += `${out}\t[ * ${+x + 1}]\r\n`;
            num.suc++;
            num.mod++;
          } else {
            this.errorInfoData += `[${+x +
              1}]\t${log}\r\n>> Error \r\n${
              thisNewData.code[x]
            }\r\n\r\n`;
            num.errall++;
            num.no++;
          }
          //delete
        } else if (thisNewData.opreation[x] == '-') {
          HaveCodeReg = new RegExp(
            `.+\\t\\b${thisNewData.code[x]}#?\\d*\\b[\\r\\n]*`,
            'g'
          );
          TermsCorrectCodeReg = new RegExp(
            `${thisNewData.word[x]}\\t\\b${
              thisNewData.code[x]
            }#?\\d*\\b[\\r\\n]*`,
            'g'
          );
          log = `[ ${thisNewData.code[x]}\t${thisNewData.opreation[x]}\t${
            thisNewData.word[x]
          } ]`;
          out = thisNewData.word[x] + '\t' + thisNewData.code[x];

          if (this.newTermsData.search(HaveCodeReg) != -1) {
            if (this.newTermsData.search(TermsCorrectCodeReg) != -1) {
              this.newTermsData = this.newTermsData.replace(
                eval(TermsCorrectCodeReg),
                ''
              );
              this.successInfoData += `${out}\t[ - ${+x + 1}]\r\n`;
              num.suc++;
              num.del++;
            } else {
              this.errorInfoData += `[${+x +
                1}]\t${log}\r\n>> Warn \r\n${this.newTermsData
                .match(HaveCodeReg)
                .join('')}\r\n\r\n`;
              num.errall++;
              num.no++;
            }
          } else {
            this.errorInfoData += `[${+x +
              1}]\t${log}\r\n>> Error \r\n${
              thisNewData.code[x]
            }\r\n\r\n`;
            num.errall++;
            num.no++;
          }
          //
        } else if (thisNewData.opreation[x] == '/') {
          log = `[ ${thisNewData.code[x]}\t${thisNewData.opreation[x]}\t${
            thisNewData.word[x]
          } ]`;
          out = thisNewData.word[x] + '\t' + thisNewData.code[x];
          this.errorInfoData += `[${+x +
            1}]\t${log}\r\n>> Info \r\n${
            thisNewData.code[x]
          }\r\n\r\n`;
          num.errall++;
          num.erratt++;
        }
      }

      this.TermsCountSet('newTermsData', 'newTermsCountData');

      let nWordNum = this.newTermsCountData.obj.word.length,
        nCodeNum = this.newTermsCountData.obj.code.length;
      //
      this.count.out = `: ${nWordNum} : ${nCodeNum}`;
      this.count.success = `${num.suc} : ${num.add} : ${num.mod} ${
        num.del
      }`;
      this.count.error = `: ${num.errall} : ${num.err} ${num.no} ${
        num.erratt
      }`;
      //
      this.clearSpace();

      //
      if (this.isDev) {
        this.outTerms = this.clearIdent(true, this.newTermsData);
      } else {
        this.outTerms = this.clearIdent(false, this.newTermsData);
      }
      this.oldTerms = this.clearIdent(false, this.oldTerms);

      //
      this.successInfoData = `\n ${num.suc} ,  ${
        num.add
      } ,\n ${num.mod} ,  ${num.del} \n\n${
        this.successInfoData
      }`;
      if (!num.errall && !num.err && !num.no && !num.erratt) {
        this.errorInfoData = '';
      } else {
        this.errorInfoData = `\n ${num.errall} ,  ${
          num.err
        } ,\n ${num.no} ,  ${num.erratt} \n\n${
          this.errorInfoData
        }`;
      }

      this.successInfo = this.successInfoData;
      this.errorInfo = this.errorInfoData;

      //
      this.$Message.destroy();
      this.$Message.success({
        content: `${num.suc}${num.errall}`,
        duration: 3,
      });
      this.$Loading.finish();

      //
      this.successInfoData = this.errorInfoData = this.newTermsData = '';
    },
    TermsCountSet(formName, formData) {
      //
      this.allToHalf();

      this[formData].test = this[formName].split(/[\t\r\n]/g);

      this[formData].add = [];
      this[formData].obj = {};
      this[formData].obj.word = [];
      this[formData].obj.code = [];
      this[formData].obj.opreation = [];
      for (var x in this[formData].test) {
        var isOpreation = /^[+\-*/]$/.test(this[formData].test[x]);
        var isCode = /^[a-z]+#?\d*$/.test(this[formData].test[x]);
        var isChinese = /[^a-z]|[^+\-*]+/.test(this[formData].test[x]);
        if (isOpreation) {
          this[formData].obj.opreation.push(this[formData].test[x]);
        } else if (isCode) {
          this[formData].obj.code.push(this[formData].test[x]);
        } else if (isChinese) {
          this[formData].obj.word.push(this[formData].test[x]);
        }
      }
    },
    testing(isHoundle) {
      var newsHaveRepeat = /#\d+/.test(this.newsTerms);

      if (0 == this.oldTerms.length) {
        this.$Loading.error();
        this.$Message.error('');
        return;
      }

      //
      this.TermsCountSet('oldTerms', 'oldTermsData');
      this.TermsCountSet('newsTerms', 'newsTermsData');

      let oWordNum = this.oldTermsData.obj.word.length,
        oCodeNum = this.oldTermsData.obj.code.length,
        mWordNum = this.newsTermsData.obj.word.length,
        mModifyNum = this.newsTermsData.obj.opreation.length,
        mCodeNum = this.newsTermsData.obj.code.length;

      this.count.old = `: ${oWordNum} : ${oCodeNum}`;
      this.count.new = `: ${mCodeNum} : ${mModifyNum} : ${mWordNum}`;

      if (oWordNum != oCodeNum) {
        this.$Loading.error();
        this.$Message.error('');
        this.count.old = `<b style="color: red;">: ${oWordNum} : ${oCodeNum}<b>`;
        return;
      }

      if (
        mWordNum != mModifyNum ||
        mModifyNum != mCodeNum ||
        mWordNum != mCodeNum
      ) {
        this.$Loading.error();
        this.$Message.error('');
        this.count.new = `<b style="color: red;">: ${mCodeNum} : ${mModifyNum} : ${mWordNum}<b>`;
        return;
      }

      //
      if (isHoundle) {
        this.handleOldTerms(newsHaveRepeat);
      } else {
        return;
      }
    },
    createDemo() {
      this.oldTerms = this.demoData.old;
      this.newsTerms = this.demoData.new;
      this.testing(false);
    },
    proStartStatus() {
      this.$Message.error('');
    },
    clearContent() {
      this.oldTerms = this.newsTerms = this.outTerms = this.newTermsData = '';
      this.oldTermsData = {};
      this.testing(false);
      this.$Loading.finish();
      this.$Message.destroy();
      this.$Message.success('');
    },
    allToHalf() {
      var regPlus = //g;
      var regMinus = /-/g;
      var regModify = /!m|m|M|!M|/g;
      var regIgnore = /\/|/g;
      this.newsTerms = this.newsTerms.replace(regPlus, '+');
      this.newsTerms = this.newsTerms.replace(regMinus, '-');
      this.newsTerms = this.newsTerms.replace(regModify, '*');
      this.newsTerms = this.newsTerms.replace(regIgnore, '/');
    },
    clearIdent(isclear, data) {
      var result = data.replace(/#\d+/g, '');
      return isclear ? data : result;
    },
    clearSpace() {
      this.newTermsData = this.newTermsData.replace(/[\r\n]\s/g, '\r');
    },
    clickHistory() {
      this.updates = !this.updates;
      this.isHover = !this.isHover;
    },
    insertTab() {
      document.execCommand('insertText', false, '	');
    },
    setCookie(cname, cvalue, exdays) {
      var d = new Date();
      d.setTime(d + exdays * 24 * 60 * 60 * 1000);
      var expires = 'expires=' + d.toGMTString();
      document.cookie = cname + '=' + cvalue + '; ' + expires;
    },
    getCookie(cname) {
      var name = cname + '=';
      var ca = document.cookie.split(';');
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim();
        if (c.indexOf(name) == 0) return c.substring(name.length, c.length);
      }
      return '';
    },
    Notice() {
      var isShowUpdate = this.getCookie('showUpdate');
      var newVer = parseFloat(this.updateHistory[this.updateHistoryLength].ver);

      if (isShowUpdate != newVer) {
        this.$Notice.open({
          title: '',
          desc: `
							${this.updateHistory[this.updateHistoryLength].cont}
							${this.updateHistory[this.updateHistoryLength].ver}
							
						`,
          duration: 0,
          onClose: () => {
            this.setCookie('showUpdate', newVer, 365);
          },
        });
      }
    },
    isPhoneFn() {
      if (document.documentElement.clientWidth <= 750) {
        this.isPhone = true;
      }
      if (!this.isPhone) {
        this.showPlate = {
          old: true,
          new: true,
          out: true,
          suc: true,
          err: true,
        };
      } else {
        this.switchControl = false;
      }
      for (let x in this.updateHistory) {
        this.updateHistoryLength = x;
      }
    },
  },
};
</script>

<style>
.main-container {
  border-radius: 0.25rem;
  overflow: hidden;
  z-index: 50;
}

.translateRotate-enter-active,
.translateRotate-leave-active {
  transition: all 0.5s;
}

.translateRotate-enter,
.translateRotate-leave-to {
  transform: translate3d(0, -10px, 10px) rotate3d(-2, 0, 0, -30deg);
  opacity: 0;
}

.slide-enter-active,
.slide-leave-active {
  transition: all 0.3s linear;
}

.slide-enter,
.slide-leave-to {
  height: 0;
  opacity: 0;
}

textarea {
  width: 100%;
  height: 200px;
  resize: none !important;
  padding: 5px 10px !important;
  border: 1px solid #e0e3da;
  border-top: 0;
  background: #fffff3;
  display: flex;
}

textarea:read-only {
  background: #fffff3;
}

textarea:focus {
  z-index: 99;
}

textarea:focus + .info {
  height: 26px;
  overflow: initial;
}

.info {
  width: 100%;
  height: 0;
  overflow: hidden;
  text-align: left;
  border: 1px solid #e0e3da;
  padding-left: 5px;
  transition: all 0.3s;
  animation: info-animate 1s ease-in-out forwards;
}

@keyframes info-animate {
  form {
    height: 0;
    overflow: hidden;
  }

  to {
    height: 26px;
    overflow: auto;
  }
}

.textarea-info {
  float: right;
  opacity: 0.7;
  margin-right: 5px;
}

.controls-switch {
  position: fixed !important;
  left: 20px;
  bottom: 150px;
  z-index: 5;
}

.controls-btn {
  position: fixed !important;
  width: 100%;
  left: 0;
  bottom: 0;
  z-index: 999;
}

.main-spin-icon-load {
  animation: ani-demo-spin 1s linear infinite;
}

.history-card .ivu-card-extra {
  top: 3px;
}

@media (min-width: 768px) {
  .controls-btn {
    position: static !important;
  }
  .controls-switch {
    display: none !important;
  }
}
</style>
